html {
  scroll-behavior: smooth; }

body {
  font-family: sans-serif;
  font-weight: 300;
  transition: all ease-in-out 250ms;
  background-color: #eee;
  overflow-y: hidden; }

/* width */
::-webkit-scrollbar {
  width: 5px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #8981d8;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #7d61af; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: sans-serif; }

p {
  font-family: sans-serif;
  font-weight: 400; }

.btn-dark {
  background-color: #6a61c0;
  border: none; }
  .btn-dark:hover {
    background-color: #8981d8; }

@media (max-width: 576px) {
  .btn {
    font-size: .9rem; } }

header {
  position: fixed;
  width: 100%;
  background-color: #fff;
  z-index: 100;
  color: #fff; }
  header .fa-search {
    color: #444;
    margin-left: 1em; }
  header input[type='search'] {
    border: none;
    padding: .5em 1em .5em .5em;
    outline: none; }
  header .left-header {
    background-color: #6a61c0;
    padding: 1.2em; }
    header .left-header p {
      margin: 0; }
  header .right-header p {
    margin: 0 1em;
    display: inline; }
  header .right-header .img-container {
    height: 3em;
    width: 3em;
    border-radius: 50%;
    overflow: hidden; }

#sidebar {
  background-color: #8981d8;
  color: #fff;
  padding: 2em 0;
  height: 100vh; }
  #sidebar ul li {
    padding: .5em 1em;
    cursor: pointer; }
    #sidebar ul li.active {
      background-color: rgba(250, 250, 250, 0.4);
      border-left: 3px solid #fff;
      padding: .5em .8em; }
    #sidebar ul li i {
      margin-right: .5em;
      font-size: 1.2em; }
  @media (max-width: 550px) {
    #sidebar {
      height: auto; } }

.category-items .nav-pills .active {
  background-color: #8981d8;
  padding: .5em 1.5em; }

.display-card-container {
  color: #8981d8;
  cursor: pointer;
  position: relative; }
  .display-card-container .add-note-card {
    color: #444;
    position: absolute;
    top: 40px;
    left: -70px;
    right: 40px;
    min-width: 22vw;
    z-index: 1; }
    .display-card-container .add-note-card textarea {
      border: 0;
      outline: none;
      resize: none; }
    .display-card-container .add-note-card textarea[name='title'] {
      font-size: 1.2em;
      width: 100%; }
    .display-card-container .add-note-card textarea[name='text'] {
      height: 100px;
      width: 100%; }

#notes-container #notes {
  padding: 0 1em 2em;
  margin-bottom: 1.5em;
  max-height: 70vh;
  overflow: scroll;
  overflow-x: hidden; }

#notes-container .card-text {
  padding-right: 0; }
  #notes-container .card-text .fa-edit {
    margin-left: 0.3em;
    margin-right: .3em; }
  #notes-container .card-text i {
    cursor: pointer;
    color: #6a61c0; }

#notes-container .card-columns {
  column-gap: 0; }
  #notes-container .card-columns .editor {
    border: solid 1px gray;
    width: 300px;
    height: 100px;
    padding: 6px;
    overflow: scroll; }
  @media (min-width: 576px) {
    #notes-container .card-columns .card {
      max-width: 21vw; } }

[contenteditable][placeholder]:empty:before {
  content: attr(placeholder);
  position: absolute;
  color: gray;
  background-color: transparent; }
